.scale-video {
  width: 100%;
  padding-top: 56.25%;
  position: relative;
}

.scale-video iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.video-background{
  position: absolute;
  width: 100%;
  padding-bottom: 56.25%;
  top: calc((var(--header-height) + var(--footer-height)) * -1);
  left: 0;
  max-height: 100vh;
  overflow: hidden;
}
.video-foreground, .video-foreground iframe, .video-foreground .layer{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  .layer{
    background: rgba(#000,.2);
  }
}
.page-content{
  color: #fff;
  background: rgba(#1D2436,.8);
  padding: 1.5em 1.5em 2em;
  position: absolute;
  left: 0;
  bottom: var(--footer-height);
  h2{
    text-transform: uppercase;
    margin-bottom: .5rem;
  }
  p{
    text-align: center;
    // min-width: ;
    margin: 0;
    @media (min-width: 1024px){
      width: 70%;
      text-align: left;
    }
  }
  &.store{
    background: rgba(#263535,.8);
  }
}
