/*
  This boilerplate is based in ITCSS and SMACSS
*/
.main-menu, .slide-navigation {
  list-style: none;
  padding: 0;
  margin: 0;
}

:root {
  --color-main: #5596cc;
  --color-white: #fdfdfd;
  --color-black: #111;
  --header-height: 70px;
  --footer-height: 2rem;
  --color-bg: #fdfdfd;
  --color-font: #222;
  --z-normal: 1;
  --z-fixed: 100;
  --z-modal: 1000;
  --time: 1s;
  --font-family: 'SF Pro Text', Sans-Serif;
  --font-title: 'Bebas Kai', Sans-Serif;
}

@font-face {
  font-family: 'SF Pro Text';
  src: url("../assets/fonts/SFProText-Regular.woff2") format("woff2"), url("../assets/fonts/SFProText-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Bebas Kai';
  src: url("../assets/fonts/BebasKai.woff2") format("woff2"), url("../assets/fonts/BebasKai.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

*,
*::before,
*:after {
  box-sizing: border-box;
}

html, body {
  font-size: 16px;
}

body {
  margin: 0;
  font-family: "SF Pro Text", Sans-Serif;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  background: var(--color-bg);
  color: #222;
  min-height: 100vh;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-family: "Bebas Kai", Sans-Serif;
}

.main-header {
  height: var(--header-height);
  position: relative;
  z-index: 1000;
  background: var(--color-bg);
  width: 100%;
}

.container {
  max-width: 1300px;
  margin: 0 auto;
  width: 90%;
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.main-logo {
  width: auto;
  height: 60%;
}

.main-nav {
  display: flex;
  align-items: center;
}

.social-network {
  display: block;
  margin-right: 1em;
  width: 2rem;
  height: 2rem;
}

.social-network svg {
  fill: #5596cc;
  transition: fill .3s;
}

.social-network:hover.facebook svg {
  fill: #3b5998;
}

.social-network:hover.instagram svg {
  fill: #ff0084;
}

.social-network:hover .icon-nav {
  stroke: #2b6290;
}

.social-network.nav {
  height: auto;
}

.social-network.nav svg {
  transition: stroke .3s;
}

.main-menu {
  position: absolute;
  top: var(--header-height);
  right: 1em;
  background: var(--color-bg);
  overflow: hidden;
  max-height: 0;
  transition: max-height .3s;
}

.main-menu a {
  display: block;
  text-align: center;
  padding: 0 2.5em;
  line-height: 4em;
}

.main-menu.show {
  max-height: calc(4em * 5);
}

.main-menu .clas {
  display: none;
}

.main-footer {
  margin-top: auto;
  padding: 0 1em;
  height: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 1000;
  background: var(--color-bg);
}

.main-footer p {
  color: red;
}

.main-footer .copy, .main-footer strong {
  color: #222;
}

.slides-container {
  position: relative;
  width: 100vw;
  height: calc(100vh - var(--header-height) - var(--footer-height));
}

.slide-item {
  position: absolute;
  width: 100vw;
  height: calc(100vh - var(--header-height) - var(--footer-height));
  transition: opacity 1s;
  opacity: 0;
}

.slide-item.active {
  opacity: 1;
}

.slide-item.active .slide-caption {
  animation: caption .5s 1s linear forwards;
}

@keyframes caption {
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.slide-item.active .text {
  transform: translateX(50%) scale(1);
  animation: capText .2s 1.5s linear forwards;
}

@keyframes capText {
  to {
    opacity: 1;
    transform: translateX(0) scale(1);
  }
}

.slide-item img {
  width: auto;
  min-width: 100vw;
  height: calc(100vh - var(--header-height) - var(--footer-height));
  object-fit: cover;
  object-position: center;
}

@media screem and (max-width: 1400px) {
  .slide-item img {
    object-position: top;
  }
}

.slide-caption {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 400px;
  top: 0;
  height: calc(100vh - var(--header-height) - var(--footer-height));
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  padding: 0 3em;
  opacity: 0;
  z-index: 250;
}

.slide-caption h2, .slide-caption p {
  position: relative;
  z-index: 300;
  margin: 0;
}

.slide-caption.blue {
  background: rgba(29, 36, 54, 0.8);
}

.slide-caption.green {
  background: rgba(80, 105, 107, 0.8);
}

.slide-caption.darkgreen {
  background: rgba(38, 53, 53, 0.8);
}

.slide-caption.grey {
  background: rgba(45, 44, 50, 0.8);
}

.slide-caption .text {
  opacity: 0;
}

.slide-caption h2 {
  margin-bottom: .5rem;
  text-transform: uppercase;
  text-align: center;
}

.slide-caption p {
  text-align: center;
}

.slide-caption.right {
  right: 0;
}

.blue {
  background: rgba(29, 36, 54, 0.8);
}

.green {
  background: rgba(80, 105, 107, 0.8);
}

.darkgreen {
  background: rgba(38, 53, 53, 0.8);
}

.grey {
  background: rgba(45, 44, 50, 0.8);
}

.slide-navigation {
  position: absolute;
  bottom: 4em;
  right: 1em;
  display: flex;
  z-index: 500;
}

.slide-navigation li {
  background: #fdfdfd;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  margin-right: .5em;
  cursor: pointer;
}

.slide-navigation li:last-child {
  margin-right: 0;
}

.slide-navigation li.active {
  background: #5596cc;
}

.scale-video {
  width: 100%;
  padding-top: 56.25%;
  position: relative;
}

.scale-video iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.video-background {
  position: absolute;
  width: 100%;
  padding-bottom: 56.25%;
  top: calc((var(--header-height) + var(--footer-height)) * -1);
  left: 0;
  max-height: 100vh;
  overflow: hidden;
}

.video-foreground, .video-foreground iframe, .video-foreground .layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.video-foreground .layer, .video-foreground iframe .layer, .video-foreground .layer .layer {
  background: rgba(0, 0, 0, 0.2);
}

.page-content {
  color: #fff;
  background: rgba(29, 36, 54, 0.8);
  padding: 1.5em 1.5em 2em;
  position: absolute;
  left: 0;
  bottom: var(--footer-height);
}

.page-content h2 {
  text-transform: uppercase;
  margin-bottom: .5rem;
}

.page-content p {
  text-align: center;
  margin: 0;
}

@media (min-width: 1024px) {
  .page-content p {
    width: 70%;
    text-align: left;
  }
}

.page-content.store {
  background: rgba(38, 53, 53, 0.8);
}

.banner-page {
  width: 100%;
  height: calc(100vh - var(--header-height) - var(--footer-height));
  position: relative;
  overflow: hidden;
}

.banner-page__img {
  width: 100%;
  height: calc(100vh - var(--header-height) - var(--footer-height));
  object-fit: cover;
  object-position: center;
}

.banner-page__overlay {
  position: absolute;
  width: 100%;
  max-width: 500px;
  max-height: calc(100vh - var(--footer-height));
  height: 100%;
  top: 0;
  background: rgba(0, 0, 0, 0.65);
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  left: 0;
}

.banner-page__overlay h2, .banner-page__overlay h3, .banner-page__overlay p {
  margin: 0;
  width: 50%;
  font-weight: normal;
  font-size: 1.7em;
}

.banner-page__overlay h2 {
  text-transform: uppercase;
  text-align: center;
}

.banner-page__overlay h2:last-of-type {
  margin-bottom: 1rem;
}

.banner-page__overlay h3 {
  text-align: left;
  font-size: 1.5em;
  text-transform: capitalize;
}

.banner-page__overlay.left {
  left: 0;
  transform: translate3d(-100%, 0, 0);
  animation: left .8s forwards;
}

@keyframes left {
  to {
    transform: translate3d(0, 0, 0);
  }
}

.banner-page__overlay.contacto {
  padding: 0 4em;
}

.banner-page__overlay.contacto h2, .banner-page__overlay.contacto h3, .banner-page__overlay.contacto p {
  width: 100%;
}

.banner-page__overlay.contacto iframe {
  width: 100%;
  height: 300px;
  border: none;
  margin-bottom: 1rem;
}

@media screen and (max-width: 760px) {
  .banner-page__overlay.contacto iframe {
    height: 200px;
  }
}

@media screen and (max-height: 760px) {
  .banner-page__overlay.contacto iframe {
    height: 250px;
    margin-top: 3rem;
  }
}

.banner-page__overlay.contacto p.address {
  font-size: .8em;
  text-align: center;
  margin-bottom: .5rem;
}

.address-container {
  margin-bottom: .5rem;
}

.address-container p {
  font-size: 1em;
  width: 100%;
}

.address-container p.title {
  font-weight: bold;
}

.address-container::last-of-type {
  margin-bottom: 0;
}

.banner-page__text {
  width: 75%;
  margin: 0 auto;
  animation: text .5s 1s forwards;
  opacity: 0;
}

@keyframes text {
  to {
    opacity: 1;
  }
}

.banner-page__text h2, .banner-page__text h3, .banner-page__text p {
  width: 100%;
}

.social-network svg {
  fill: #5596cc;
  transition: fill .3s;
}

.social-network:hover.facebook svg {
  fill: #3b5998;
}

.social-network:hover.instagram svg {
  fill: #ff0084;
}
