.banner-page{
  width: 100%;
  height: calc(100vh - var(--header-height) - var(--footer-height));
  
  position: relative;
  overflow: hidden;
  &__img{
    width: 100%;
    height: calc(100vh - var(--header-height) - var(--footer-height));
    object-fit: cover;
    object-position: center;
  }
  &__overlay{
    position: absolute;
    width: 100%;
    max-width: 500px;
    max-height: calc(100vh - var(--footer-height));
    height: 100%;
    top: 0;
    background: rgba(#000,.65);
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    left: 0;
    h2,h3,p{
      margin: 0;
      width: 50%;
      font-weight: normal;
      font-size: 1.7em;
    }
    h2{
      text-transform: uppercase;
      text-align: center;
      &:last-of-type{
        margin-bottom: 1rem;
      }
    }
    h3{
      text-align: left;
      font-size: 1.5em;
      text-transform: capitalize;
    }

    &.left{
      left: 0;
      transform: translate3d(-100%,0,0);
      animation: left .8s forwards;
      @keyframes left {
        to {
          transform: translate3d(0,0,0);
        }
      }
    }
    &.contacto{
      padding: 0 4em;
      @media screen and (max-height: 760px){
      }
      h2,h3,p{
        width: 100%;
      }
      iframe{
        width: 100%;
        height: 300px;
        border: none;
        margin-bottom: 1rem;
        @media screen and (max-width: 760px){
          height: 200px
        }
        @media screen and (max-height: 760px){
          height: 250px;
          margin-top: 3rem;
        }
      }
      p.address{
        font-size: .8em;
        text-align: center;
        margin-bottom: .5rem;
      }
    }
  }
  
}
.address-container{
  margin-bottom: .5rem;
  p{
    font-size: 1em;
    width: 100%;
    &.title{
      font-weight: bold;
    }
  }
  &::last-of-type{
    margin-bottom: 0;
  }
}
.banner-page__text{
  // background: darkolivegreen;
  width: 75%;
  margin: 0 auto;
  animation: text .5s 1s forwards;
  opacity: 0;
  @keyframes text {
    to {
      opacity: 1;
    }
  }
  h2, h3, p{
    width: 100%;
  }
}
