.social-network{
  svg{
    fill: $color-main;
    transition: fill .3s;
  }
  &:hover{
    &.facebook{
      svg{
        fill: #3b5998;
      }
    }
    &.instagram{
      svg{
        fill: #ff0084;
      }
    }
  }
}
